// content
.content{
    padding-bottom: 156px;
}
//collection
.collection{
    padding-top:40px;
    margin-bottom:100px;
    .collection-title{
        text-align:center;
        h2{
            line-height:1.5;
            font-size:$font-size-m*2.1;
            @include mobile{
                font-size:$font-size-m;
            }
        }
        p{
            margin-bottom: 40px;
            @include mobile{
                font-size:$font-size-s;
                margin-bottom: 20px;
            }
        }
       
    }
    .collection-list{
        display: flex;
        flex-wrap: wrap;
        line-height: 1.5;
        >li{
            width: 31.333%;
            margin-bottom: 104px;
            @media(max-width:998px){
                width: 48%;
                margin-bottom: 72px;
            }
            @include pad{
                width: 100%;
                margin-bottom: 32px;
            }
            img{
                width:100%;
                height: 164px;
                object-fit: cover;
            }
            .list-price{
                font-size:18px;
                @include mobile{
                    font-size: $font-size-s;
                }
            }
            .square{
                li{
                    width:18px;
                    height:18px;
                    margin-right: 8px;
                    border-radius: 20%;
                    @include mobile{
                        width: 15px;
                        height: 15px;
                    }
                }
                li:first-of-type{
                    background: #5F3E2D ;
                }
            }
        }
    }
}   
