// content
.content{
   padding-bottom: 156px;
}
// Celluloid
.Celluloid{
    padding-top:40px;
    margin-bottom:100px;
    .Celluloid-title{
        text-align:center;
        h2{
            line-height:1.5;
            font-size:$font-size-m*2.1;
            @include mobile{
                font-size:$font-size-m;
            }
        }
        p{
            margin-bottom: 40px;
            @include mobile{
                font-size:$font-size-s;
                margin-bottom: 20px;
            }
        }
       
    }
    .Celluloid-list{
        display: flex;
        flex-wrap: wrap;
        line-height: 1.5;
        >li{
            width: 23%;
            margin-bottom: 104px;
            @media(max-width:998px){
                width: 48%;
                margin-bottom: 72px;
            }
            @include pad{
                width: 100%;
                margin-bottom: 32px;
            }
            img{
                width:100%;
                height: 164px;
                object-fit: cover;
            }
            .list-price{
                justify-content: space-between;
                font-size: 24px;
                @include mobile{
                    font-size: $font-size-s;
                }
            }
            .square{
                li{
                    width:24px;
                    height:24px;
                    margin-right: 8px;
                    border-radius: 20%;
                    @include mobile{
                        width: 15px;
                        height: 15px;
                    }
                }
                li:first-of-type{
                    background: #5F3E2D ;
                }
            }
        }
    }
}
// select-page
.select-page{
    font-size: $font-size-s+3;
    @include mobile{
        font-size: 12px;
    }
    li{
        padding: 12px;
        border: 1px solid #555555;
        color:#555555;
        cursor: pointer;
        @include pad{
            padding: 8px;
        }
    }
    li:first-of-type,
    li:last-of-type{
        background: #F2F2F2;
    }
    li:hover{
        background:#000000;
        color:#fff;
    }
}
