.menu-list-container
{
    max-width:1386px;
    margin: 0 auto;
}
// menu-list
.menu-list{
    font-size: 42px;
    font-weight: bold;
    flex-wrap: wrap;
    cursor: pointer;
    @media(max-width:992px){
        font-size: 28px; 
    }
    @include pad{
        font-size: $font-size-m;
    }
    @include mobile{
        font-size:14px;
    }
    li{
        width: 33.33%;
        border-bottom: 16px solid  transparent;
        border-left: 1px solid #DCDCDC;
        border-right: 1px solid #DCDCDC;
        @include mobile{
            width: 98%;
            border-left: none;
            border-right: none;
        }
    }
    li:hover{
        border-bottom: 16px solid  #AA0601;
        @media(max-width:992px){
            border-bottom: 8px solid  #AA0601;
        }
    }
    img{
        height: 48px;
        margin-right: 8px;
        @media(max-width:992px){
            height: 32px;
        }
        @include pad{
            height: 20px;
        }
        @include mobile{
            height: 18px;
        }
    }
    a{
        color:#000;
        padding: 24px 0px;
        text-decoration: none;
        @include pad{
            padding: 12px 0px;
        }
        @include mobile{
            padding: 10px 0px;
        }
    }
}
// banner
.banner{
    display: flex;
    flex-wrap: wrap;
    li{
        width: 49.5%;
        @media(max-width:992px){
            width: 50%;
            height: 309px;
        }
        @include pad{
            height: 144px;
        }
        @include mobile{
            width: 100%;
        }
    }
    img{
        width: 100%;
        height: 100%;
    }

} 
