// banner
.location-banner{
    background-image: url('../images/week4/qa.png');
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    height: 294px;
    padding-bottom: 10px;
    @include mobile{
       height: 143px;
    }
    h2{
        margin-right: 24px;
        @include pad{
            font-size: $font-size-m*2.4;
            margin-right: $font-size-s;
        }
        @include mobile{
            font-size:$font-size-m*1.4;
            margin-right: $font-size-s/2;
        }
    }
    img{
        @include pad{
            width: 30px;
            height: 43px;
        }
        @include mobile{
            width: 16px;
            height: 24px;
        }
    }
    
}
// shop-location
.shop-location{
    padding-top: 20px;
    padding-bottom: 304px;
    .select-location{
        align-items: center;
        margin-bottom: 32px;
        padding-left: 15px;
        label{
            font-size:$font-size-m+4;
            margin-right: 20px;
            white-space:nowrap;
            @include pad{
                font-size: $font-size-s;
            }
            @include iphone{
                font-size: 14px;
            }
        }
        select{
            width:324px;
            height: 45px;
            font-size: 18px;
            color:#495057;
        }
    }
    .location-list{
        flex-wrap: wrap;
        >li{
            width: 31.333%;
            margin-bottom: 52px;
            padding: 0px 15px;
            @media(max-width:992px){
               width: 48%;
            }
            @include pad{
                width:100% ;
            }
            img{
                width: 100%;
                height: 224px;
                object-fit: cover;
                object-position: 50% 50%;
                @include mobile{
                    height: 164px;
                }
             }
             .card-title {
                 font-size: $font-size-m+4;
                 height: 60px;
                 border: 1px solid #00000020;
                 padding-left: 20px;
                 @include pad{
                    font-size: $font-size-m;
                 }
                 @include mobile{
                    font-size: $font-size-m;
                 }
             }
             .card-body{
                border: 1px solid #00000020;
                padding: 16px 28px 16px 24px;
                li{
                    font-size: $font-size-m;
                    margin-bottom: 10px;
                    @include pad{
                        align-items: center;
                        p{font-size: 14px;}
                    }
                    img{
                        width: 23px;
                        height: 23px;
                        margin-right: 12px;
                    }
                }   
            }
            a{
                text-decoration: none;
                display: block;
                background: black;
                color:#fff;
                font-size: 19px;
                padding: 20px;
                @include pad{
                    font-size: 14px;
                }
            }
            a:hover{
                background:orange;
                font-weight: bold;
                color:black;
            }
        }
        
        
    }
}
